@font-face {
  font-family: "Golos";
  src: url('../fonts/Golos/GolosText-Regular.woff2') format('woff2'), url('../fonts/Golos/GolosText-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Golos";
  src: url('../fonts/Golos/GolosText-Medium.woff2') format('woff2'), url('../fonts/Golos/GolosText-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Golos";
  src: url('../fonts/Golos/GolosText-SemiBold.woff2') format('woff2'), url('../fonts/Golos/GolosText-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Golos";
  src: url('../fonts/Golos/GolosText-Bold.woff2') format('woff2'), url('../fonts/Golos/GolosText-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "PT Root UI";
  src: url('../fonts/PT Root UI/PTRootUI-Regular.woff2') format('woff2'), url('../fonts/PT Root UI/PTRootUI-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "PT Root UI";
  src: url('../fonts/PT Root UI/PTRootUI-Medium.woff2') format('woff2'), url('../fonts/PT Root UI/PTRootUI-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "PT Root UI";
  src: url('../fonts/PT Root UI/PTRootUI-Bold.woff2') format('woff2'), url('../fonts/PT Root UI/PTRootUI-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  overflow-x: hidden;
  color: #2D264B;
}

body {
  background-color: #DCDCDC;
  font-family: "PT Root UI";
  font-size: 16px;
  line-height: 20px;
}

a {
  font-family: inherit;
  display: inline-block;
  text-decoration: none;
  color: #2D264B;
}

address {
  color: inherit;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 65px;
  padding-right: 65px;
}

.vh {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.h1,
.h2,
.h3 {
  font-family: "Golos";
  font-weight: 500;
}

.h1 {
  font-size: 86px;
  line-height: 104px;
  font-weight: 600;
}

.h2 {
  font-size: 36px;
  line-height: 43px;
}

.h3 {
  font-size: 20px;
  line-height: 24px;
}

.button {
  color: #FD4C00;
  padding: 12px 32px;
  background: none;
  border: 1px solid #FD4C00;
  border-radius: 30px;
  font-family: inherit;
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.5s ease;
}

.button:hover {
  border-color: #F95C19;
  color: #F95C19;
}

.button--filled {
  background-color: #FD4C00;
  color: #FFF;
}

.button--filled:hover {
  background-color: #F95C19;
  color: #FFF;
}

.header__top {
  display: flex;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid #C6C6CA;
}

.header__topNav {
  margin-left: auto;
}

.header__info {
  display: flex;
}

.header__text {
  color: #5C5D7A;
  margin-right: 40px;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
}

.header__link {
  font-size: 20px;
  line-height: 25px;
  font-weight: 500;
  color: #35375B;
}

.header__link:last-child {
  margin-left: 40px;
}

.header__bottom {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: auto;
}

.header__order {
  margin-left: 40px;
}

.nav__body {
  display: flex;
}

.nav__link {
  margin-left: 40px;
  font-size: 18px;
  line-height: 22px;
  color: #35375B;
  transition: color 0.5s ease, fill 0.5s ease;
}

.nav__link:hover {
  color: #FD4C00;
}

.nav__link:hover svg {
  fill: #FD4C00;
}

.nav__link--services {
  display: flex;
}

.nav__link--services .nav__icon {
  margin-left: 10px;
}

.nav__link--services:hover .nav__icon {
  transform: rotate(180deg) translateY(-4px);
}

.nav__link--catalog::before {
  content: '';
  display: inline-block;
  width: 21px;
  height: 17px;
  background: url(../img/icon-catalog.svg) no-repeat center / contain;
  margin-right: 10px;
  transform: translateY(2px);
}

.list {
  font-size: 16px;
  line-height: 20px;
  color: #35375B;
}

.list__item {
  margin-bottom: 24px;
  padding-left: 40px;
  position: relative;
}

.list__item span {
  font-weight: 500;
}

.list__item::before {
  content: '';
  display: inline-block;
  width: 28px;
  height: 28px;
  margin-right: 16px;
  background: url(../img/icon-check.svg) no-repeat center / contain;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.list__item--shield::before {
  background-image: url(../img/icon-shield.svg);
}

.list__item--award::before {
  background-image: url(../img/icon-award.svg);
}

.list__item--dollar::before {
  background-image: url(../img/icon-dollar.svg);
}

.list__item--document::before {
  background-image: url(../img/icon-document.svg);
}

.service {
  padding: 120px 0 0;
  position: relative;
}

.service .container {
  box-sizing: border-box;
}

.service__mainTitle {
  max-width: 564px;
}

.service__wrapper {
  margin-top: 60px;
  display: grid;
  grid-auto-flow: row dense;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 10px;
  grid-template-areas: ". . remont" "arenda arenda remont" ". . .";
}

.service__wrapper::before {
  content: '';
  display: block;
  width: 687px;
  height: 620px;
  background: url(../img/services-element-bg.svg) no-repeat center / contain;
  position: absolute;
  left: 0;
}

.service__wrapper::after {
  content: '';
  display: block;
  width: 749px;
  height: 768px;
  background: url(../img/services-element-bg.svg) no-repeat center / contain;
  position: absolute;
  right: 150px;
  top: 20%;
}

.service__item {
  padding: 20px;
  min-height: 235px;
  background: rgba(227, 225, 225, 0.6);
  border: 1px solid #EFEFEF;
  border-radius: 10px;
  box-sizing: border-box;
  position: relative;
}

.service__item:hover {
  background: rgba(227, 225, 225, 0.8);
}

.service__item--remont {
  grid-area: remont;
}

.service__item--remont::after {
  content: '';
  display: block;
  width: 268px;
  height: 300px;
  background: url(../img/services-remont.svg) no-repeat center / contain;
  position: absolute;
  right: 50px;
  bottom: 16px;
}

.service__item--arenda {
  grid-area: arenda;
}

.service__item--arenda::after {
  content: '';
  display: block;
  width: 224px;
  height: 200px;
  background: url(../img/services-arenda.svg) no-repeat center / contain;
  position: absolute;
  top: 20px;
  right: 50px;
}

.service__itemWrapper {
  display: flex;
  flex-direction: column;
  max-width: 390px;
  max-height: 192px;
  height: 100%;
}

.service__title {
  max-width: 90%;
  font-size: 22px;
  line-height: 26px;
}

.service__text {
  margin-top: auto;
}

.service__bottom {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.service__price {
  font-size: 30px;
  line-height: 37px;
  color: #FD4C00;
}

.proposition {
  margin: 100px auto;
  max-width: 1090px;
  padding: 40px 50px;
  background: ;
  border: 1px solid #EFEFEF;
  background: #E4E4E4 url(../img/proposition-image.svg) no-repeat 90% top / 33%;
}

.proposition__title {
  max-width: 600px;
  font-family: "Golos";
  font-size: 30px;
  line-height: 36px;
  font-weight: 500;
  margin-bottom: 30px;
}

.intro {
  padding: 100px 0 250px;
  background: url(../img/main-intro-bg.jpg) no-repeat center bottom / 90%;
}

.intro__wrapper {
  display: flex;
  justify-content: space-between;
}

.intro__title {
  text-transform: uppercase;
}

.intro__title span {
  display: block;
  text-transform: none;
  font-size: 38px;
  line-height: 45px;
}

.intro__list {
  margin-top: 66px;
}

.intro__offer {
  margin-right: 80px;
}

.intro__offerItem {
  max-width: 320px;
  padding: 15px 20px;
  background: #E4E4E4;
  border-radius: 10px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.intro__offerText {
  margin: 20px 0 14px;
}

.intro__offerBtn {
  width: 100%;
}

.greeting__wrapper {
  display: flex;
  justify-content: center;
}

.greeting__title {
  max-width: 540px;
  margin-left: 120px;
}

.advantage {
  padding: 20px 0;
}

.advantage__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.advantage__title {
  max-width: 480px;
}

.advantage__text {
  margin-top: 30px;
  font-size: 18px;
  line-height: 22px;
}

.advantage__text p {
  margin-bottom: 22px;
}

.advantage__dialog {
  display: flex;
  align-items: flex-end;
  margin-right: 110px;
}

.advantage__dialogImg {
  width: 100px;
  height: 100px;
}

.advantage__dialogMessage {
  width: 100%;
  max-width: 514px;
  min-height: 150px;
  padding: 20px;
  padding-left: 114px;
  margin-left: 36px;
  box-sizing: border-box;
  background: url(../img/advantage-dialog.svg) no-repeat right center / contain;
}

.advantage__dialogHead {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.advantage__dialogTime {
  font-size: 14px;
  line-height: 17px;
  color: #848498;
}

.advantage__dialogText {
  margin-top: 30px;
  font-size: 18px;
  line-height: 22px;
}

.advantage__list {
  display: flex;
  flex-wrap: wrap;
  margin: 90px 100px 0 auto;
  max-width: 660px;
}

.advantage__item {
  width: calc(50% - 10px);
  margin-bottom: 72px;
  padding-right: 10px;
}

.advantage__itemTitle {
  font-size: 50px;
  line-height: 60px;
  color: #FD4C00;
}

.advantage__itemText {
  margin-top: 12px;
}